* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 100vh;
  opacity: 1;
  /* background: url(https://img.freepik.com/free-photo/election-concept-person-dropping-ballot-card-into-vote-box-dark-cinematic-tone_34048-1399.jpg?size=626&ext=jpg); */
  background: #c5e7eb;
  background-repeat: no-repeat;
  background-size: cover;
}
.heading {
  margin-top: 40px;
  color: #475cc9;
}
.heading h1 {
  color: #475cc9;
}
.container {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 40px 0;
  width: 100%;
}
.container .card {
  position: relative;
  width: 55%;
  height: 500px;
  background: #85b3d1ff;
  margin: 20px;
  /* box-shadow: 0 15px 35px rgba(255, 255, 255, 0.171); */
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 1s;
  box-shadow: 0 5px 10px rgba(34, 34, 34, 0.527), 0 15px 40px rgba(0, 0, 0, 0.2);
}
i {
  color: black;
}
.container .card .content {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 1;
  transition: 0.5s;
  color: #000000;
}
.container .card .content .img {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  border: 10px solid #475cc9;
  background: #000000d0;
}
.container .card .content .img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.container .card .content .contentBx {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  margin: 20px 0 10px;
  line-height: 1.1em;
  color: #2b3a86;
}
.container .card .content .contentBx h3 {
  font-size: 24px;
  font-weight: 700;
}
.container .card .content .contentBx h3 span {
  font-size: 20px;
  font-weight: 600;
}
.container .card .sci {
  position: absolute;
  bottom: 50px;
  display: flex;
}
.container .card .sci li {
  list-style: none;
  margin: 0 10px;
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
  transition: 0.5s;
  opacity: 0;
  transition-delay: calc(0.1s * var(--i));
}
.container .card .sci li a {
  color: #fff;
  font-size: 24px;
}

@media (max-width: 767px) {
  .one {
    flex-direction: column;
    justify-content: center;
    margin: 0 10px;
  }
  .about {
    width: 90%;
  }
  .container .card {
    width: 90%;
  }
}
.mat {
  color: #32418d;
  font-size: 24px;
  font-weight: 600;
}

.dm {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.main-container {
  min-height: 100vh;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.main-container h1 {
  margin: 0 0 80px 0;
  color: #475cc9;
  font-size: 40px;
  font-family: sans-serif;
  font-weight: 700;
  text-align: center;
  margin-top: 20px;
}
.radio-buttons {
  width: 100%;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}
.custom-radio input {
  display: none;
}
.radio-btn {
  margin: 10px;
  width: 300px;
  height: 200px;
  border: 3px solid transparent;
  display: inline-block;
  border-radius: 10px;
  position: relative;
  text-align: center;
  box-shadow: 0 0 20px #c3c3c367;
  background: #637aecbe;
  cursor: pointer;
}
.radio-btn > i {
  color: #ffffff;
  background-color: #000000;
  font-size: 20px;
  position: absolute;
  top: -15px;
  left: 50%;
  -webkit-transform: translateX(-50%) scale(4);
          transform: translateX(-50%) scale(4);
  border-radius: 50px;
  padding: 3px;
  transition: 0.2s;
  pointer-events: none;
  opacity: 0;
}
.radio-btn {
  align-items: center;
  text-align: center;
}
.radio-btn h2 {
  display: flex;
  flex-wrap: wrap;
  /* width: 90%; */
  justify-content: center;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  align-self: center;
  font-family: sans-serif;
}
.custom-radio input:checked + .radio-btn {
  border: 3px solid #000000;
}
.custom-radio input:checked + .radio-btn > i {
  opacity: 1;
  -webkit-transform: translateX(-50%) scale(1);
          transform: translateX(-50%) scale(1);
}

button {
  bottom: 0;
  margin-top: 50px;
  width: 130px;
  height: 40px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  outline: none;
  color: white;
  font-size: 20px;
  margin-bottom: 50px;
  background: #475cc9;
  transition: all 0.3s ease;
}
button:hover {
  background: #475dc9cb;
}
.timer-container {
  flex-basis: 1;
  flex-shrink: inherit;
  margin: 0;
  padding: 0;
  width: 100%;
  background: #475dc917;
  min-height: 100px;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  box-shadow: 10px 10px 30px rgb(88, 88, 88);
}
.timer {
  align-items: center;
  display: flex;
  flex-direction: column;
}
.timer div {
  margin-top: 10px;
}
.time {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
}
.time section {
  padding: 5px;
}
.timer i {
  font-size: 20px;
}
.timer h2 {
  font-size: 24px;
  color: #475cc9;
}
p {
  font-family: sans-serif;
  font-size: 20px;
  font-weight: 500;
}

.chart {
  display: block;
  margin-top: 50px;
  width: 90%;
}

